//home page animation
const mainTitle1 = document.querySelector('.home-main-title__name--first');
const mainTitle2 = document.querySelector('.home-main-title__name--second');
const logo = document.querySelector('.logo');
const mainLine = document.querySelector('.home-main-line');

window.onload = () => {
  const mainTitle1Anim = anime({
    targets: mainTitle1,
    translateX: ['-150%', '-50%'],
    opacity: [0, 1],
    autoplay: true,
    delay: 900,
    easing: 'linear',
    duration: 200
  });

  const mainTitle2Anim = anime({
    targets: mainTitle2,
    translateX: ['150%', '-50%'],
    opacity: [0, 1],
    autoplay: true,
    delay: 900,
    easing: 'linear',
    duration: 500
  });

  const logoAnim = anime({
    targets: logo,
    opacity: [0, 1],
    autoplay: true,
    delay: 1500,
    easing: 'linear',
    duration: 800
  });

  const mainLineAnim = anime({
    targets: mainLine,
    translateX: '-50%',
    translateY: '-50%',
    scale: [0, 1],
    autoplay: true,
    delay: 1900,
    easing: 'linear',
    duration: 500
  });
}

//about img animation
const aboutImg = document.querySelector('.about-main__img-wrapper');

if(aboutImg) {
  window.onload = () => {
    const aboutImgAnim = anime({
      targets: aboutImg,
      translateX: ['-150%', '0%'],
      autoplay: true,
      delay: 2000,
      easing: 'spring(1, 80, 10, 0)',
      duration: 500,
    });
  }
}

//works links animation
let worksLinkAnim;

if(window.matchMedia("(min-width: 992px)").matches) {
  worksLinkAnim = anime({
    targets: '.works__link',
    translateY: ['150%', '0%'],
    easing: 'linear',
    duration: 600,
    delay: anime.stagger(200, {start: 2500}),
  });
} else {
  worksLinkAnim = anime({
    targets: '.works__link',
    translateX: ['-400%', '0%'],
    easing: 'linear',
    duration: 600,
    delay: anime.stagger(200, {start: 2500}),
  });
}


//article tasks and list animation
const articlePrevTasks = document.querySelectorAll('.article-preview__task');

articlePrevTasks.forEach(task => {
  const waypoint = new Waypoint({
    element: task,
    handler: function() {
      const taskTranslation = anime({
        targets: task,
        opacity: [0, 1],
        translateY: ['50%', '0%'],
        easing: 'linear',
        duration: 500
      });
    },
    offset: '100%',
  })
})

const previewList  = document.querySelectorAll('.article-preview__list');
const articlePreview = document.querySelector('.article-preview');
const workMain = document.querySelector('.work-main');
let previewItem;
let listAnim;

previewList.forEach(list => {
  const listScroll = new Waypoint({
    element: list,
    handler: function() {
      if(articlePreview) {
        previewItem = document.querySelectorAll('.article-preview .article-preview__item');
        listAnim = anime({
          targets: previewItem,
          opacity: [0, 1],
          easing: 'linear',
          duration: 200,
          delay: anime.stagger(200),
        });
      } else if(workMain) {
        previewItem = document.querySelectorAll('.work-main .article-preview__item');
        listAnim = anime({
          targets: previewItem,
          opacity: [0, 1],
          easing: 'linear',
          duration: 200,
          delay: anime.stagger(200, {start: 2500}),
        });
      }
    },
    offset: '100%',
  })
})

//work animation

const workScaleBig = document.querySelectorAll('.ScaleBigJs');
const workScaleMini = document.querySelectorAll('.ScaleMiniJs');

workScaleBig.forEach(item => {
  const workImgScroll = new Waypoint({
    element: item,
    handler: function() {
      const taskTranslation = anime({
        targets: item,
        scale: [1.5, 1],
        easing: 'linear',
        duration: 1500
      });
    },
    offset: '99%',
  })
})

workScaleMini.forEach(item => {
  const workImgScroll = new Waypoint({
    element: item,
    handler: function() {
      const taskTranslation = anime({
        targets: item,
        scale: [0.7, 1],
        easing: 'linear',
        duration: 500
      });
    },
    offset: '100%',
  })
})